<template>
  <div class="bussiness-records">
    <div class="topTitle">
      <span class="topTitle-span1">洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">业务洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">洗钱风险评估记录</span>
      <div class="button-content">
        <!-- <Button type="primary" @click="operate('add')">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
        <Button type="primary">显示</Button>
        <Button type="primary">排序</Button>-->
      </div>
    </div>
    <div class="content2">
      <Form
        ref="formValidate"
        :model="formValidate"
        :rules="ruleValidate"
        :label-width="120"
        inline
      >
        <Row>
          <FormItem label="业务部门:" prop="busiScope">
            <treeselect
              v-model="formValidate.extraUnit"
              valueConsistsOf="ALL" 
              :limit="3"
              :multiple="true"
              :options="data1"
              placeholder="请选择"
              :max-height="100"
              style="width:200px"
            />
          </FormItem>
          <FormItem label="业务名称:" prop>
            <Input v-model="formValidate.formname" placeholder="请输入" style="width:200px"></Input>
          </FormItem>
          <FormItem label="评估日期:" prop="xxxx">
            <DatePicker
              :value="formValidate.value2"
              format="yyyy/MM/dd"
              type="daterange"
              placement="bottom-end"
              placeholder="请选择时间"
              style="width: 200px"
              @on-change="value2fun"
            ></DatePicker>
          </FormItem>
          <FormItem label="调整日期:" prop="xxxx">
            <DatePicker
              :value="formValidate.value3"
              format="yyyy/MM/dd"
              type="daterange"
              placement="bottom-start"
              placeholder="请选择时间"
              style="width: 200px"
              @on-change="value3fun"
            ></DatePicker>
          </FormItem>
          <FormItem label="系统评估等级:" prop>
            <Select v-model="formValidate.formname2" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option
                v-for="item in riskLevel"
                :value="item.levelCode"
                :key="item.levelCode"
              >{{item.levelName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="洗钱风险等级:" prop>
            <Select v-model="formValidate.formname3" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option
                v-for="item in riskLevel"
                :value="item.levelCode"
                :key="item.levelCode"
              >{{item.levelName}}</Option>
            </Select>
          </FormItem>
          <FormItem label="评估审核状态:" prop>
            <Select v-model="formValidate.formname4" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in eval" :value="item.code" :key="item.code">{{item.name}}</Option>n
            </Select>
          </FormItem>
          <FormItem label="调整审核状态:" prop>
            <Select v-model="formValidate.formname5" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in adj" :value="item.code" :key="item.code">{{item.name}}</Option>n
            </Select>
          </FormItem>
        </Row>
        <Row>
          <FormItem label="业务类型:" prop>
            <div class="header-wrapper">
              <Tag
                @click.native="changeSts(index,item.checked)"
                checkable
                v-for="(item, index) in businessTypes"
                :checked="item.checked"
                :key="index"
              >{{item.name}}</Tag>
            </div>
          </FormItem>
        </Row>
        <Row>
          <div class="btnClass">
            <Button type="primary" @click="handleSubmit('formValidate',0)" style="width:89px">查询</Button>
            <Button
              type="primary"
              style="margin-left:10px"
              @click="handleSubmit('formValidate',1)"
            >精确查询</Button>
          </div>
        </Row>
      </Form>
    </div>
    <div class="contentCellBody">
      <Row>
        <div class="content1">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>数据列表
          </div>
        </div>
      </Row>
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in tableBody">
              <td>{{pageSize*(pageNub-1)+index+1}}</td>
              <td>{{item.department}}</td>
              <td>{{item.busCode}}</td>
              <td>
                <span
                  class="spanBox"
                  @click="assessDetail(item.busId,item.newBusId,item.type,item.evalBusinessId,item.adjBusinessId)"
                >{{item.busName}}</span>
              </td>
              <td>{{item.busType}}</td>
              <td>{{item.createTime?item.createTime.substr(0,10):''}}</td>
              <td>{{item.type}}</td>
              <td>{{item.totalScore}}</td>
              <td>{{item.sysLevel}}</td>
              <td>{{item.evalDate?item.evalDate.substr(0,10):''}}</td>
              <td>{{item.evalUpdater}}</td>
              <td>{{item.riskLevel}}</td>
              <td class="textOverflow textmaxwidth" :title='item.evalRemark'>{{item.evalRemark}}</td>
              
              <td class="spanBox" @click="watchFiles(item.evalBusinessId)">查看</td>
              <td>{{item.evalStatus}}</td>
              <td>{{item.adjStatus}}</td>
              <td>{{item.adjustLevel}}</td>
              <td>{{item.adjTime?item.adjTime.substr(0,10):''}}</td>
              <td>{{item.adjCreator}}</td>
              <!-- <td>{{item.causeType}}</td> -->
          
                <td class="textOverflow textmaxwidth" :title="item.causeDetail">{{item.causeDetail}}</td>
           
              <td class="spanBox" @click="watchFiles(item.adjBusinessId)">查看</td>
              <td>{{item.flag}}</td>
              <td
                class="spanBox"
                @click="watchdetaile(item.type,item.evalBusinessId,item.adjBusinessId)"
              >查看详情</td>
              <!-- type==1的时候evalBusinessId等于2的时候adjBusinessId -->
            </tr>
          </tbody>
        </table>
      </Row>
    </div>
    <div class="flexCenter pageBox">
      <Page
        :total="rowCount"
        :current.sync="pageNub"
        :page-size="pageSize"
        @on-change="pageChange"
        @on-page-size-change="pageSizefun"
        show-elevator
        show-sizer
      />
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: percent}"
    >
      <component :is="tabview" v-if="showModal" :data-objj="dataObjj"></component>
      <div slot="footer"></div>
    </Modal>
    <Modal :mask-closable="false"   v-model="modal1" title="文件列表">
      <div class="fujian">
        <div class="fujian-title">附件:</div>
        <div v-for="(item,index) in fileList" :key="index">
          <span
            style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
            class="cursorPointer"
            @click="fileDownload(item.fileId, item.filename)"
          >{{item.filename}}</span>
        </div>
      </div>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import processMessage from "../common/historyDetaill/historyMessage"; //过程信息
import assessDetail from "../common/assessDetail"; //
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect, processMessage, assessDetail },
  created() {
    let that = this;
    this.tablePage();
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/EVAL_STA,BUS_TYPE,ADJ_STA", {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          response.data.BUS_TYPE.forEach(element => {
            element.checked = false;
          });
          that.businessTypes = response.data.BUS_TYPE;
           that.adj= response.data.ADJ_STA;
          that.eval= response.data.EVAL_STA;
        }
      })
      .catch(error => {});
    this.axios
      .post("/risk/busi/getRiskLevel", {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.riskLevel = response.data;
        }
      })
      .catch(error => {});
  },
  data() {
    return {
      dataObjj: "", //弹框数据
      showModal: false, //弹框控制
      modal1: false,
      fileList: [],
      modelTitle: "", //弹框标题
      percent: "80%", //弹框宽度
      tabview: "", //弹框引入组件名称
      evalDate: "", //评估日期1
      evalDate2: "", //评估日期2
      adjDate: "", //调整日期1
      adjDate2: "", //调整日期2
      riskLevel: [], //风险等级
      //业务类型
      businessTypes: [],
      eval: [], //评估待审核
      adj: [], //调整待审核
      searchFlag: "0", //查询标注

      rowCount: 100, //总条数
      pageSize: 20, //每页多少条
      pageNub: 1,
      tabname: "name1",
      data1: [],
      tableHeader: [
        { name: "序" },
        { name: "业务部门" },
        { name: "业务代码" },
        { name: "业务名称" },
        { name: "业务类型" },
        {name:"创建时间"},
        { name: "评估类型" },
        { name: "综合评分" },
        { name: "系统评估等级" },
        { name: "评估日期" },
        { name: "评估人员" },
        { name: "洗钱风险等级" },
        { name: "评估备注" },
        { name: "评估附件" },
        { name: "评估审核状态" },
        { name: "调整审核状态" },
        { name: "调整等级" },
        { name: "调整日期" },
        { name: "调整人员" },
        // { name: "调整事由" },
        { name: "调整事由说明" },
        { name: "调整附件" },
        { name: "状态" },
        { name: "流程详情" }
      ],
      tableBody: [],
      formValidate: {
        busiScope: "", //业务范围
        formname: "", //业务名称
        value2: [], //评估日期
        value3: [], //调整日期
        formname3: "", //风险等级
        formname2: "", //系统评估等级
        formname4: "", //评估审核状态:
        formname5: "", //调整审核状态:
        busType: [], //业务类型
        extraUnit: [] //业务部门

        // -----
      },
      // formValidate1: {
      //   busiScope: "", //业务范围
      //   formname: "", //申请人
      //   permissionTime: "", //申请时间
      //   permissionTime1: "",
      //   permissionTime3: "", //截止时间
      //   permissionTime4: "",
      //   formname3: "" //紧急程度

      //   // -----
      // },
      ruleValidate: {
        busiScope: [
          {
            required: false,
            message: "请选择业务",
            trigger: "change"
          }
        ],
        formname: [{ required: false, message: "请输入", trigger: "blur" }],
        permissionTime: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime1: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime3: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime4: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        formname3: [
          {
            required: false,
            message: "请选择时间",
            trigger: "change"
          }
        ]
      }
      // ruleValidate1: {
      //   busiScope: [
      //     {
      //       required: true,
      //       message: "请选择业务",
      //       trigger: "change"
      //     }
      //   ],
      //   formname: [{ required: true, message: "请输入", trigger: "blur" }],
      //   permissionTime: [
      //     {
      //       required: true,
      //       message: "请选择时间",
      //       trigger: "change"
      //     }
      //   ],
      //   permissionTime1: [
      //     {
      //       required: true,
      //       message: "请选择时间",
      //       trigger: "change"
      //     }
      //   ],
      //   permissionTime3: [
      //     {
      //       required: true,
      //       message: "请选择时间",
      //       trigger: "change"
      //     }
      //   ],
      //   permissionTime4: [
      //     {
      //       required: true,
      //       message: "请选择时间",
      //       trigger: "change"
      //     }
      //   ],
      //   formname3: [
      //     {
      //       required: true,
      //       message: "请选择时间",
      //       trigger: "change"
      //     }
      //   ]
      // }
    };
  },
  methods: {
    watchdetaile(a, b, c) {
      this.dataObjj = {
        type: a,
        evalBusinessId: b,
        adjBusinessId: c
      };
      this.tabview = "processMessage";
      this.modelTitle = "流程信息";
      this.showModal = true;
      this.percent = "80%";
    },
    cancel() {},
    watchFiles(id) {
      this.fileList = [];
      if(id){
        this.axios
        .post(`/system/remote/getFileList/${id}`)
        .then(res => {
          if (res.code == "200") {
            this.fileList = res.data;
          }
        })
        .catch(function(error) {});
      }
      this.modal1 = true;
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    value2fun(a) {
      this.formValidate.value2 = a;
      if (this.formValidate.value2.length > 0) {
        this.evalDate = this.formValidate.value2[0];
        this.evalDate1 = this.formValidate.value2[1];
      }
    },
    value3fun(a) {
      this.formValidate.value3 = a;
      if (this.formValidate.value3.length > 0) {
        this.adjDate = this.formValidate.value3[0];
        this.adjDate1 = this.formValidate.value3[1];
      }
    },
    tablePage() {
      let that = this;
      let data = {
        pageNum: this.pageNub,
        pageSize: this.pageSize,
        flag: this.searchFlag,
        department: this.formValidate.extraUnit, //业务部门
        busName: this.formValidate.formname, //业务名称
        evalDate: this.evalDate,
        evalDate1: this.evalDate1,
        adjDate: this.adjDate,
        adjDate1: this.adjDate1,
        sysLevel: this.formValidate.formname2, //系统评估等级
        riskLevel: this.formValidate.formname3, //风险等级
        evalStatu: this.formValidate.formname4, //评估审核状态
        adjStatu: this.formValidate.formname5, //调整审核状态
        busType: this.formValidate.busType //业务类型
      };
      data = JSON.stringify(data);
      this.axios
        .post("/risk/busi/busRiskEvalHisRecord", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          that.rowCount = response.data.total;
          that.tableBody = response.data.records;
        })
        .catch(error => {});
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    pageSizefun(a) {
      this.pageSize = a;
      this.tablePage();
    },
    assessDetail(a, b, c, d, e) {
      this.dataObjj = {
        busId: a,
        newBusId: b,
        type: c,
        evalBusinessId: d,
        adjBusinessId: e
      };
      this.tabview = "assessDetail";
      this.modelTitle = "评估详情";
      this.showModal = true;
      this.percent = "80%";
    },
    changeSts(index, param) {
      this.businessTypes[index].checked = !this.businessTypes[index].checked;
      let businessType = [];
      this.businessTypes.forEach(e => {
        if (e.checked) businessType.push(e.code);
      });
      this.formValidate.busType = businessType;
    },
    verifyDetail() {
      this.$router.push({
        name: "taskAuditDataExtractionApplyDetail",
        query: { tabname: "name1" }
      });
    },
    flowDetail() {
      this.$router.push({
        name: "taskAuditDataExtractionApplyDetail",
        query: { tabname: "name2" }
      });
    },
    handleSubmit(name, exactquery) {
      let that = this;
      this.$refs[name].validate(valid => {
        if (valid) {
          that.searchFlag = exactquery;
          that.pageNub=1;
          that.tablePage();
        }
      });
    },
    resetRequest(name) {
      if (name == "name1") {
        //发起第一个页请求
      } else if (name == "name2") {
        //发起第二个页请求
      }
    },
    pageChange(pageNo) {
      let that = this;
      that.pageNub = pageNo;
      that.tablePage();
    }
  }
};
</script>
<style lang="scss" scoped>
.ivu-tag {
  background-color: #fa9324;
}
.textOverBox {
  max-width: 150px !important;
  height: 40px;
  white-space: nowrap;
  line-height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.bussiness-records {
  background: #fff;
  .content2 {
    padding: 20px 0;
    // border-top: 1px solid #eef3f8;
    .content1 {
      margin-left: 35px;
      .contentCellTitle {
        display: inline-block;
        width: 100px;
        font-size: 16px;
        vertical-align: top;
        .contentCellTitle-1 {
          width: 3px;
          height: 18px;
        }
      }
      .header-wrapper {
        width: 80%;
        display: inline-block;
        padding: 5px 0;
        .ivu-tag {
          padding: 0px 5px;
          margin: 11px 4px 4px 0px;
          height: 28px;
          line-height: 28px;
          background: #fa9324;
          &.ivu-tag-checked {
            span {
              &.ivu-tag-text {
                color: #fff;
              }
            }
          }
          span {
            &.ivu-tag-text {
              color: #fff;
            }
          }
          &:not(.ivu-tag-border):not(.ivu-tag-dot):not(.ivu-tag-checked) {
            background: 0 0;
            span {
              &.ivu-tag-text {
                color: #b6b6b6;
              }
            }
          }
        }
        span {
          font-size: 14px;
          margin: 0 10px;
          cursor: pointer;
          &.active {
            background: #fa9324;
            border-radius: 2px;
            color: #fff;
          }
        }
      }
    }
  }
  .contentCellBody {
    padding: 0 10px;
    .tableBox {
      thead {
        td {
          width: 100px;
        }
      }
    }
  }
  .btnClass {
    text-align: center;
  }
}
</style>



